import ImagemPerfil03 from './Imagens/fumante.jpg';
import ImagemPerfil04 from './Imagens/hermione.jpg';
import ImagemPerfil05 from './Imagens/homemaranha.jpg';
import ImagemPerfil06 from './Imagens/mavis.png';


export default function ladodireito() {
    return(
        <div className='ladodireito'>

            <h3 className='titulo'>Suggestions For You</h3>
            
            <div className='sugestoes'>
                <Iten 

                    recomend={ImagemPerfil01}
                    nome='Dates_para_Casais'
                    informa='Suggestions For You'
                    seguir='Follow'
                
                />
                
                <Iten 

                    recomend={ImagemPerfil02}
                    nome='Art_for_your_life'
                    informa='Suggestions For You'
                    seguir='Follow'
            
                />
                
                <Iten 

                    recomend={ImagemPerfil03}
                    nome='Bad_boy'
                    informa='Suggestions For You'
                    seguir='Follow'
        
                />
                
                <Iten 

                    recomend={ImagemPerfil04}
                    nome='Potter_Head'
                    informa='Suggestions For You'
                    seguir='Follow'
        
                />
                
                <Iten 

                    recomend={ImagemPerfil05}
                    nome='SpiderMan'
                    informa='Suggestions For You'
                    seguir='Follow'
    
                />
                
                <Iten 

                    recomend={ImagemPerfil06}
                    nome='Mavis_102'
                    informa='Suggestions For You'
                    seguir='Follow'

                />
            


            </div>
        </div>
    )
}